<template>
  <footer class="page-footer mt-4">
    <div class="">
      <b-row>
        <b-col class="text-left" cols="1">
          <b-link><i data-feather="facebook"></i></b-link>
        </b-col>
        <b-col class="text-left" cols="1">
          <b-link><i data-feather="instagram"></i></b-link>
        </b-col>
        <b-col class="text-left" cols="1">
          <b-link><i data-feather="mail"></i></b-link>
        </b-col>
        <b-col class="text-right" cols="3" offset="6">
          <span>Zenwood &copy 2018</span>
        </b-col>
      </b-row>
    </div>
  </footer>
</template>
<script>
  export default {
    name: 'BottomFooter',
    mounted () {
      this.$feather.replace()
    }
  }
</script>
<style>

/* Sticky footer styles
html {
  position: relative;
  min-height: 100%;
}
body {
  margin-bottom: 60px; /* Margin bottom by footer height
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px; /* Set the fixed height of the footer here
  line-height: 60px; /* Vertically center the text there
  background-color: #f5f5f5;
}
-------------------------------------------------- */

</style>



